import React, { useEffect, useState } from 'react';
import { Button, Divider, Input, Layout, Table, message} from 'antd'
import type { ColumnsType } from 'antd/lib/table';
import styles from './styles.module.scss'
import { utils as xlsxUtils, writeFileXLSX } from 'xlsx';
import {nanoid} from 'nanoid'


interface SheetLineObj {
  category: string,
  weight: number | string | undefined,
  school: string,
  key?: string
}

const tableColumns: ColumnsType<SheetLineObj> = [
  {
    title: 'category',
    dataIndex: 'category',
  },
  {
    title: 'weight',
    dataIndex: 'weight',
  },
  {
    title: 'school',
    dataIndex: 'school',
  },
];
const today = new Date(),
nextDay = new Date();
nextDay.setDate(today.getDate() - 1)
const dateStr = `${(nextDay).getMonth() + 1}-${(nextDay).getDate()}`

const TextToExcel = () => {
  const { TextArea } = Input
  const { Content, Footer } = Layout
  const excelWorkBook = xlsxUtils.book_new()
  const [tableData, setTableData] = useState<SheetLineObj[]>([])
  const [inputStr, setInputStr] = useState('')
  const [badLines, setBadLines] = useState<String[]>([])

  const onTextAreaChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    let rawStr = e.target.value || ''
    setInputStr(rawStr)
  }

  const onTransfer = () => {
    const lines = inputStr.replace(/\n[\n]+/g, '\n').split('\n')
    // TODO: setState 闭包问题
    const badLinesTemp: String[] = []

    lines.forEach((cur, idx) => {
      // 去除空格
      cur = cur.replace(/\s*/g, '')
      // 匹配 [数字(小数)][（学校）]
      const weightSchoolPatternStr = '(\\d+(?:\\.[\\d]+)?)（([^（）]+)）'
      // 匹配行末的说明，附加在最后一个学校名后面
      // 例如 带皮五花肉 10（10中1）8（税务1）印章少一点 的「印章...」合并为 school: 税务1 印章少一点
      const tailSpecPattern = '(.*?)'
      // 校验当前行是否合法
      const legalPattern = new RegExp(`^(.+?)(${weightSchoolPatternStr})+${tailSpecPattern}$`)
      if (!legalPattern.test(cur)) {
        console.log(`失败行数 ${cur}`);
        badLinesTemp.push(cur)
        return
      }
      const matches = cur.match(legalPattern)
      if (!matches?.length) {
        return
      }
      // 当前配货品类
      const category = matches[1]
      // 当前行补充说明
      const tailSpec = matches[5] || ''

      let execRes: Array<any> | null = []

      let firstOfCate = true
      const weightSchoolPattern = new RegExp(weightSchoolPatternStr, 'g')
      const ordersOfLine: SheetLineObj[] = []
      while ((execRes = weightSchoolPattern.exec(cur)) !== null) {
        const [_Hit, weight, school] = execRes
        ordersOfLine.push({
          category: firstOfCate ? category : '',
          weight,
          school,
          key: nanoid()
        })
        if (firstOfCate) {
          firstOfCate = false
        }
      }
      if (tailSpec && ordersOfLine.length) {
        let newSchool = ordersOfLine[ordersOfLine.length - 1].school + ' ' + tailSpec
        ordersOfLine[ordersOfLine.length - 1].school = newSchool
      }

      setTableData((oldData) => {
        return [...oldData, ...ordersOfLine]
      })
    })

    setBadLines([...badLinesTemp])
    setTableData((oldData) => {
      return [{
        category: `${dateStr} 绿贝贝`,
        weight: '数量',
        school: '学校',
        key: nanoid()
      }, ...oldData,]
    })
  }

  const onExport = () => {
    if (!tableData.length) {
      return message.error('表格数据为空，请先转换下单子')
    }
    const workSheet = xlsxUtils.json_to_sheet(tableData.map(({category, weight, school}) => ({category, weight, school})), {
      skipHeader: true
    })
    xlsxUtils.book_append_sheet(excelWorkBook, workSheet, '绿贝贝')

    // 写文件
    writeFileXLSX(excelWorkBook, `绿贝贝 ${dateStr}.xlsx`)
  }

  const onReset = () => {
    setInputStr('');
    setTableData([]);
    setBadLines([]);

  }

  /**
   * 模式: cate number
   */
  return (
    <div className={styles.tte}>
      <h2>绿贝贝</h2>
      <Layout style={{ flexDirection: 'row', backgroundColor: '#fff', flex: '1 0 auto' }}>
        <Content className="left" style={{ display: 'flex', flexDirection: 'column' }}>
          <div className='topBtnArea'>
            <div className="submitBtn" onClick={onTransfer}>转换</div>
            <div className="resetBtn" onClick={onReset}>清空</div>
          </div>
          <TextArea
            showCount
            style={{ display: 'block', flex: '1 0 auto' }}
            value={inputStr}
            onChange={onTextAreaChange}
            placeholder="can resize"
          />
        </Content>
        <Content className='right' style={{ display: 'flex', flexDirection: 'column' }}>
          <div className='topBtnArea'>
            <div className="saveBtn" onClick={onExport}>导出为表格</div>
          </div>
          {
            badLines.length ? (
              <div>
                <h2>失败行数: {badLines.length}</h2>
                <div>
                  {
                    (badLines.map((line, idx) =>
                      <div key={nanoid()}>{line}</div>
                    ))
                  }
                </div>
              </div>
            ): null
          }
          <div style={{ flex: '1 0 auto' }}>
            <Table showHeader={false} columns={tableColumns} dataSource={tableData} pagination={false} size='small'/>
          </div>
        </Content>
      </Layout>
    </div>
  );
}

export default TextToExcel;
