import * as React from "react";
import { Outlet, Link, useLocation, useNavigate, createBrowserRouter, RouterProvider } from "react-router-dom";
import TextToExcel from "./components/TextToExcel";
import { MenuInfo } from "rc-menu/lib/interface"
import { Layout, Menu, Breadcrumb } from "antd";
import styles from './App.module.scss';


const { Header, Content, Footer } = Layout


function NoMatch() {
  return (
    <div>
      <h2>Nothing to see here!</h2>
      <p>
        <Link to="/">Go to the home page</Link>
      </p>
    </div>
  );
}


const MyHome = () => {
  const navigate = useNavigate()

  const level2Paths = [
    {
      path: 'lvbeibei',
      label: '绿贝贝'
    },
    // {
    //   path: 'lingteng',
    //   label: '凌腾'
    // }
  ]

  const breadcrumbNameMap: Record<string, string> = level2Paths.reduce((acc, cur) => (Object.assign(acc, {
    [cur.path]: cur.label
  })), {})

  const location = useLocation();
  const pathSnippets = location.pathname.split('/').filter(i => i);

  const extraBreadcrumbItems = pathSnippets.map((_, index) => {
    const url = `${pathSnippets.slice(0, index + 1).join('/')}`;
    return (
      <Breadcrumb.Item key={url}>
        <Link to={url}>{breadcrumbNameMap[url]}</Link>
      </Breadcrumb.Item>
    );
  });

  const breadcrumbItems = [
    <Breadcrumb.Item key="home">
      <Link to="/">做单工具</Link>
    </Breadcrumb.Item>,
  ].concat(extraBreadcrumbItems);

  const jumpTo = ({ key: targetPath }: MenuInfo) => {
    navigate(`/${targetPath}`)
  }

  const layoutStyles: Record<string, React.CSSProperties> = {
    footer: {
      textAlign: 'center',
      position: 'fixed',
      width: '100vw',
      bottom: 0
    },
    content: {
      padding: '70px 24px 100px',
      background: '#fff',
      flex: '1 0 auto',
      height: 'calc(100vh - 170px)'
    },
    header: { position: 'fixed', zIndex: 1, width: '100%' }
  }

  return (
    <Layout className="root-layout">
      <Header style={{ ...layoutStyles.header }}>
        <div className="logo" />
        <Menu
          theme="dark"
          style={{ alignItems: 'start' }}
          mode="horizontal"
          defaultSelectedKeys={['2']}
          onClick={jumpTo}
          items={level2Paths.map(({ path, label }) => ({
            key: path,
            label,
            style: { paddingLeft: 0 }
          }))}
        />
      </Header>
      <Content className="site-layout" style={layoutStyles.content}>
        <Breadcrumb>{breadcrumbItems}</Breadcrumb>

        <Outlet />
      </Content>
      <Footer style={layoutStyles.footer}>@CopyRight</Footer>
    </Layout>
  )
}


const routerPaths = [
  {
    path: '/',
    element: <MyHome />,
    errorElement: <NoMatch />,
    children: [{
      path: 'lvbeibei',
      element: <TextToExcel></TextToExcel>
    }]
  }
]

export default function AppWrapper() {
  const router = createBrowserRouter(routerPaths)

  return (
    <RouterProvider router={router} />
  )
}